.sectionheader {
    height: 96%;
    width: 96%;
    position: absolute;
    margin-top: 2%;
    margin-left: 2%;
    margin-right: 2%;

    font-weight: 600;

    & span {
        justify-content: center;
        align-items: start;
        padding-top: 5%; }
    @include fontstart; }

.sozialnav {
    position: absolute;
    height: 96%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    color: white;

    & ul {
        height: 40%;
        width: 4rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-right: 2%;
 } }        // background-color: blue

.sozialborder {

    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    margin-left: 0;
    margin-right: 0;
    // background-color: grey
    border-right: 2px solid black; }

.sozialicon {
    height: 2rem;
    width: 2rem;
    text-decoration: none;
    background-repeat: no-repeat;
    background-position: center; }



.video-container {
    height: 100%;
    width: 100%;
    position: absolute;
    display: flex;
    justify-content: center; }

.video {
    width: 90vw;
    height: auto; }


